// Banner Section Starts Here
.banner-section {
	padding-top: 130px;
	padding-bottom: 100px;
	overflow: hidden;
	@include breakpoint(max-lg) {
		padding-top: 80px;
		padding-bottom: 80px;
	}
	@include breakpoint(max-md) {
		padding-top: 70px;
		padding-bottom: 70px;
	}
}
.banner-content {
	@include breakpoint(md) {
		max-width: 50%;
	}
	@include breakpoint(max-md) {
		margin-top: 60px;
		text-align: center;
	}
	&__subtitle {
		margin-bottom: 10px;
		@include breakpoint(md) {
			margin-bottom: 20px;
		}
	}
	&__title {
		margin-bottom: 10px;
		@include breakpoint(md) {
			margin-bottom: 20px;
		}
	}
	.cmn--btn {
		margin-top: 20px;
		@include breakpoint(md) {
			margin-top: 30px;
		}
	}
}

.banner-thumb,
.section-thumb {
	position: relative;
	padding-left: 20px;
	@include breakpoint(md) {
		max-width: 50%;
		padding-left: 40px;
	}
	@include breakpoint(max-sm) {
		margin-right: 15px;
	}

	&::before {
		position: absolute;
		content: "";
		height: 150px;
		left: 150px;
		top: -25px;
		right: -25px;
		background-color: hsl(var(--base) / 0.3);
		@include breakpoint(md) {
			height: 300px;
			left: 150px;
			top: -40px;
			right: -40px;
		}
		@include breakpoint(sm) {
			height: 250px;
		}
	}
	&::after {
		position: absolute;
		content: "";
		height: 150px;
		left: 100px;
		bottom: -20px;
		right: -15px;
		background-color: hsl(var(--base) / 0.3);
		@include breakpoint(md) {
			height: 300px;
			left: 150px;
			bottom: -30px;
			right: -25px;
		}
		@include breakpoint(sm) {
			height: 250px;
		}
	}
	.bg-shape {
		position: absolute;
		height: 200px;
		left: 25px;
		bottom: -10px;
		background-color: hsl(var(--base) / 0.3);
		width: 200px;
		@include breakpoint(md) {
			height: 150px;
			left: 15px;
			bottom: -15px;
			width: 300px;
		}
		@include breakpoint(max-md) {
			left: 5px;
		}
	}
	img {
		position: relative;
		z-index: 1;
	}
}
.section-thumb {
	max-width: 100%;
	margin-right: 0;
	&::after,
	&::before {
		height: 160px;
		right: 0;
		@include breakpoint(max-sm) {
			height: 115px;
		}
	}
	&::before {
		top: -30px;
		width: 350px;
		left: auto;
		@include breakpoint(max-sm) {
			width: 250px;
		}
	}
	&::after {
		width: 83px;
		height: 81px;
		left: auto;
		bottom: -25px;
		right: 10px;
		animation: pulse 2.5s linear infinite;
		@include breakpoint(max-sm) {
			bottom: -15px;
		}
	}
	img {
		padding-right: 35px;
		@include breakpoint(max-sm) {
			padding-right: 17px;
		}
	}
	.bg-shape {
		@include breakpoint(max-sm) {
			width: 210px;
			height: 90px !important;
		}
	}
}
.guide-thumb {
	&::before,
	&::after {
		height: 340px;
		width: 325px;
		@include breakpoint(max-sm) {
			max-width: 227px;
			max-height: 238px;
		}
	}
	&::after {
		top: -15px;
		left: 100px;
		height: 200px;
		width: 280px;
		@include breakpoint(max-lg) {
			left: 205px;
			width: 350px;
			height: 100px;
		}
		@include breakpoint(max-md) {
			width: 200px;
			left: 50px;
		}
	}
}
// Banner Section Ends Here

// Inner Banner Starts Here
.inner-banner {
	.inner-banner-thumb {
		@include breakpoint(max-md) {
			img {
				min-height: 230px;
				object-fit: cover;
			}
		}
		@include breakpoint(lg) {
			margin-top: 33px;
		}
	}
}
// Inner Banner Ends Here

// New Added Css for Update 2 Home pages
.banner-two {
	padding-top: 130px;
	padding-bottom: 100px;
	overflow: hidden;
	@include breakpoint(max-lg) {
		padding-top: 80px;
		padding-bottom: 80px;
	}
	@include breakpoint(max-md) {
		padding-top: 70px;
		padding-bottom: 70px;
	}
}
.banner-content-two {
	&__left {
		max-width: 500px;
		@include breakpoint(max-xl) {
			max-width: 450px;
		}
		@include breakpoint(max-lg) {
			max-width: 650px;
			margin-inline: auto;
			text-align: center;
		}
		.banner-title {
			font-weight: 500;
		}
		.banner-text {
			margin-top: 20px;
			margin-bottom: 30px;
		}
	}
	&__right {
		width: calc(100% - 500px);
		@include breakpoint(max-xl) {
			width: calc(100% - 450px);
		}
		@include breakpoint(max-lg) {
			width: 100%;
			margin-bottom: 45px;
		}
	}
}
// New Added Css for Update 2 Home pages


